.landing-page {
	font-family: var(--font-family);
	padding: 6rem 1rem;
}

.landing-cta {
	margin: 4rem 0 8rem;
	margin-top: 7rem;
	margin-left: 10rem;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	gap: 3rem;
}

.landing-cta .input-item {
	max-width: 20rem;
	min-width: 10rem;
	display: flex;
	align-items: center;
	gap: 2rem;
}

.landing-cta .desc {
	color: var(--color-text);
	font-weight: 600;
	font-size: 1rem;
}